.details-container {
    color: #fff;
    background-color: rgba(rgb(40, 37, 37), green, blue, alpha);
    display: flex;
    gap: 2rem;
    line-height: 1.8;

}

.details-container >* {
    flex-grow: auto;
}

.columns {
    display: flex;
    gap: 1rem;
}

.columns >* {
    flex: 1;
}

.subInfo-bar >*:not(:nth-child(4)) {
    border-right: 2px solid blueviolet;
}

